/**
 * @description  controllers
 */

module.exports = window.angular.module('controllers', [])

// @ngInject
.controller('AppCtrl', function() {})

// @ngInject
.controller('HomeCtrl', function($scope, $rootScope, foModal, $injector) {

  $scope.open = function() {
    foModal.open({
      templateUrl: 'modal.html',
      // templateUrl: 'views/modal2.html',
      controller: 'myModalCtrl as vm',
      // controllerAs: 'vm',
      // scope: $scope,
      // id: 'hello',
      // class: 'hello'
      // animation: true,
      // overlay: true,
      // showClose: false,
      // closeByOverlay: true,
      // fixBody: false,
    });
  };

  $rootScope.close = function() {
    foModal.close('hello');
    console.log('close');
  };
})


// @ngInject
.controller('myModalCtrl', function($scope) {
  var vm = this;
  $scope.yes = function() {
    console.log('yes!');
  };
});



