import 'angular';
import 'angular-route';
import './controllers';
import './directives';
import './services';
import './filters';

angular.module('app', [
  'ui.router',
  'foModal',

  'controllers',
  'directives',
  'services',
  'filters'
])

.config(function($stateProvider, $urlRouterProvider) {

  $urlRouterProvider.otherwise('/');
  $stateProvider
    .state('home', {
      url: '/',
      templateUrl: 'views/home.html',
      controller: 'HomeCtrl'
    });
});



